#fullpage .section#about .result_board > div.profile > .wrap {
  width: 490px;
}
#fullpage .section#about .result_board > div.profile > .wrap > div.text .span {
  margin-right: 20px;
  font-weight: 900;
}
#fullpage .section#about .result_board > div.profile > .wrap > div.text .p {
  font-size: 2.4rem;
  margin-bottom: 10px;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 500;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  .history {
  font-size: 1.8rem;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > p:last-of-type {
  margin-bottom: 0;
}
#fullpage .section#about .result_board > div.profile > .wrap > div.text > p a {
  color: #222;
  position: relative;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > p
  a
  span {
  position: absolute;
  top: -30px;
  right: -70px;
  background: #222;
  padding: 2px 6px 4px;
  border-radius: 3px;
  color: #fff;
  font-size: 1.1rem;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > p
  a
  span:before {
  content: "";
  width: 0px;
  height: 0px;
  display: block;
  border-top: 7px solid #222;
  border-bottom: 5px solid none;
  border-right: 7px solid transparent;
  border-left: 0px solid transparent;
  position: absolute;
  bottom: -7px;
  left: 7px;
}
#fullpage .section#about .result_board > div.profile > .wrap > div.text > span {
  font-size: 2rem;
  font-weight: bold;
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 14px;
  margin-bottom: 14px;
}
#fullpage .section#about .result_board > div.profile > .wrap > div.text > div {
  width: 100%;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > div:after {
  content: "";
  display: block;
  clear: both;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > div
  > div {
  float: left;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > div
  > div:nth-of-type(1) {
  margin-right: 20px;
  margin-bottom: 10px;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > div
  > div:nth-of-type(1)
  > p {
  font-size: 2.4rem;
}
#fullpage
  .section#about
  .result_board
  > div.profile
  > .wrap
  > div.text
  > div
