#fullpage .section#about .result_board > div {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 40px;
}
#fullpage .section#about .result_board > div > .wrap {
  position: absolute;
  top: 50%;
  left: 52%;
  transform: translate(-50%, -50%);
}
#fullpage .section#about .result_board > div > .wrap > div.text {
  width: 100%;
}
#fullpage .section#about .result_board > div > .wrap > div.text > p.title {
  font-size: 5.4rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 60px;
}
#fullpage .section#about .result_board > div.skill > .wrap .icon {
  width: 100%;
  text-align: center;
  margin-bottom: 40px;
}
#fullpage .section#about .result_board > div.skill > .wrap .icon > div {
  display: inline-block;
  margin-right: 10px;
  width: 100px;
}
#fullpage .section#about .result_board > div.skill > .wrap .icon > div > .img {
  width: 70px;
  height: 80px;
  display: block;
  margin: 0 auto 7px;
  position: relative;
}
#fullpage
  .section#about
  .result_board
  > div.skill
  > .wrap
  .icon
  > div
  > .img
  > img {
  width: 100%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#fullpage .section#about .result_board > div.skill > .wrap .icon > div > p {
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 5px;
}
#fullpage .section#about .result_board > div.skill > .wrap .icon > div > span {
  display: block;
  width: 90%;
  height: 10px;
  border: 1px solid #222;
  border-radius: 5px;
  margin: 0 auto;
}
#fullpage
  .section#about
  .result_board
  > div.skill
  > .wrap
  .icon
  > div
  > span
  > span {
  display: block;
  height: 100%;
  background: #222;
  border-radius: 4px;
}
#fullpage
  .section#about
  .result_board
  > div.skill
  > .wrap
  .icon
  > div:last-of-type {
  margin-right: 0;
}
#fullpage .section#about .result_board > div.skill > .wrap .desc {
  width: 100%;
}
#fullpage .section#about .result_board > div.skill > .wrap .desc p {
  font-size: 2rem;
  margin-bottom: 16px;
  line-height: 1.4;
}
