@charset "utf-8";
@import url("https://fonts.googleapis.com/css?family=Rubik:300,400,700");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");

* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  vertical-align: top;
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  font-size: 0.625em;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}
body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  font: 14px "Rubik", "ë‚˜ëˆ”ê³ ë”•", "ë‹ì›€", Dotum, Helvetica,
    "Apple SD Gothic Neo", "sans-serif";
  color: #222;
  overflow: hidden;
}
main {
  display: block;
}
.main {
  flex: 1 0 auto;
  width: 100%;
}
footer {
  flex-shrink: 0;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  background-color: transparent;
}
img {
  border: none;
}
.blind {
  display: none !important;
  width: 0 !important;
  height: 0 !important;
}
