#board > #about > div.detail > .board_menu {
  width: 100%;
}
#board > #about > div.detail > .board_menu > div {
  width: 100%;
  height: 50px;
  margin-bottom: 14px;
}
#board > #about > div.detail > .board_menu > div:last-of-type {
  margin-bottom: 0;
}
#board > #about > div.detail > .board_menu > div.two:after {
  content: "";
  display: block;
  clear: both;
}
#board > #about > div.detail > .board_menu > div > div {
  font-weight: bold;
  line-height: 50px;
  border: 2px solid #222;
  font-size: 2.2rem;
  text-align: center;
  cursor: pointer;
}
#board > #about > div.detail > .board_menu > div.one > div {
  width: 100%;
  height: 100%;
}
#board > #about > div.detail > .board_menu > div.two > div {
  width: 48%;
  height: 100%;
  float: left;
}
#board > #about > div.detail > .board_menu > div.two > div:nth-of-type(1) {
  margin-right: 4%;
}
#board > #about > div.detail > .board_menu > div > div:hover {
  background: #222;
  color: #fff;
  transition: 0.3s;
}
#board > #about > div.detail > .board_menu > div > div.on {
  background: #222;
  color: #fff;
}
